import React, { useState, useRef } from 'react'
import { useEffect } from 'react'

const Player = (props) => {
    const audioRef = useRef(null)
    const [songInfo, setSongInfo] = useState({ currentTime: 0, duration: 0 })
    const [percentage, setPercentage] = useState(0)

    const playSong = () => {
        if (props.isPlaying) {
            audioRef.current.play();
            props.setIsPlaying(!props.isPlaying);
        }
        else {
            audioRef.current.pause();
            props.setIsPlaying(!props.isPlaying);
        }
    }

    const timeUpdateHandler = (e) => {
        let currentTime = e.target.currentTime;
        let duration = e.target.duration;
        setPercentage(Math.round(currentTime/duration*100))
        setSongInfo({ ...songInfo, currentTime: currentTime, duration: duration })
        if (currentTime === duration) {
            changeSong();
            playSong();
        }
    }

    const changeSong = () => {
        let index = props.songs.findIndex((item) => item.id === props.currentSong.id)
        props.setCurrentSong(props.songs[index+1])
    }

    const timeFormat = (time) => {
            return Math.floor(time / 60) + ":" + ("0" + Math.floor(time % 60)).slice(-2)
    }

    const handleDrag = (e) => {
        audioRef.current.currentTime = e.target.value;
    }

    const handleBack = () => {
        const songIndex = props.songs.findIndex((item) => item.id === props.currentSong.id)
        if (songIndex === 0) {
            props.setCurrentSong(props.songs[props.songs.length - 1])
        }
        else {
            props.setCurrentSong(props.songs[songIndex - 1])
        }
    }

    const handleNext = () => {
        const songIndex = props.songs.findIndex((item) => item.id === props.currentSong.id)
        if (songIndex === props.songs.length - 1) {
            props.setCurrentSong(props.songs[0])
        }
        else {
            props.setCurrentSong(props.songs[songIndex + 1])
        }
    }

    useEffect(() => {
        let newSongs = props.songs.map((item) => {
            if (item.id === props.currentSong.id) {
                return { ...item, active: true }
            }
            else {
                return { ...item, active: false }
            }
        })
        props.setSongs(newSongs)
    }, [props.currentSong])

    let percentMove = {transform: `translateX(${percentage}%)`}
    let trackStyle = {background:`linear-gradient(to right, ${props.currentSong.color[0]}, ${props.currentSong.color[1]})`}

    return (
            <div className='player'>
                <div className='time-control'>
                    <p>{timeFormat(songInfo.currentTime)}</p>
                    <div className='track' style={trackStyle}>
                        <input type="range" min={0} max={songInfo.duration || 0} value={songInfo.currentTime} onChange={handleDrag} />
                        <div className='animate-track' style={percentMove}></div>
                    </div>
                    <p>{timeFormat(songInfo.duration)}</p>
                </div>
                <div className='play-control'>
                    <i className="bi bi-skip-backward" onClick={handleBack}></i>
                    {props.isPlaying ? <i className="bi bi-play" onClick={playSong}></i> : <i className="bi bi-pause-fill" onClick={playSong}></i>}
                    <i className="bi bi-skip-forward" onClick={handleNext}></i>
                </div>
                <audio onLoadedMetadata={timeUpdateHandler} onTimeUpdate={timeUpdateHandler} src={props.currentSong.audio} ref={audioRef} />
            </div>
    );
}

export default Player;
