import React, { } from 'react'

const SongItem = (props) => {
    const handleClick = () => {
        const selectedSong = props.songs.filter((item) => item.id === props.song.id);
        props.setCurrentSong(selectedSong[0]);
        let newSongs = props.songs.map((item) => {
            if (item.id === props.song.id) {
                return {...item, active:true}
            }
            else {
                return {...item, active:false}
            }
        })
        props.setSongs(newSongs)
    }
    return (
        <div className={`songItem-container ${props.song.active ? 'selectedItem' : ''}`} onClick={handleClick}>
            <img src={props.song.cover} alt={props.song.name}></img>
            <div>
                <h4>{props.song.name}</h4>
                <h5>{props.song.artist}</h5>
            </div>
        </div>
    );
}

export default SongItem;