import React from 'react';

const Nav = (props) => {
    const toggleButton = () => {
        props.setToggle(!props.toggle)
    }

    return (
        <div className='nav'>
            <h1>Wave</h1>
            <button onClick={toggleButton}>
                <span>music list</span>
                <i class="bi bi-music-note-list"></i>
            </button>
        </div>
    )
}

export default Nav