import React, {useState} from 'react'
import './styles/app.scss'
import Song from './components/Song';
import Player from './components/Player';
import data from './data';
import SongList from './components/SongList'
import Nav from './components/Nav';

function App() {
  const [songs, setSongs] = useState(data());
  const [currentSong,setCurrentSong] = useState(songs[0]);
  const [isPlaying, setIsPlaying] = useState(true);
  const [toggle, setToggle] = useState(false);
  
  let appStyle;
  if (toggle === true) {
    if (window.innerWidth >= 768) {
      appStyle = {...appStyle, marginLeft:"20%"}
    }
    else {
      appStyle = {...appStyle, marginLeft:"0"} ;
    }
  }
  else {
    appStyle = {...appStyle, marginLeft:"0"} ;
  }

  return (
    <div className="App" style={appStyle}>
      <Nav toggle={toggle} setToggle={setToggle} />
      <Song currentSong={currentSong} />
      <Player songs={songs} setSongs={setSongs} setCurrentSong={setCurrentSong} currentSong={currentSong} isPlaying={isPlaying} setIsPlaying={setIsPlaying} />
      <SongList toggle={toggle} songs={songs} setCurrentSong={setCurrentSong} setSongs={setSongs}/>
    </div>
  );
}

export default App;
