import React from 'react';
import SongItem from './SongItem';

const SongList = (props) => {
    return (
        <div className={`songList ${props.toggle ? '':'toggleList'}`}>
            <div className='songListHeader'>{window.innerWidth >= 500 ? <h1>List of Songs</h1>:<h1></h1>}</div>
            {props.songs.map((song) => <SongItem song={song} songs={props.songs} key={song.id} setSongs={props.setSongs} setCurrentSong={props.setCurrentSong}/>)}
        </div>
    )
}

export default SongList





